<template>
    <container-wrapper :designer="designer" :widget="widget" :parent-widget="parentWidget" :parent-list="parentList"
                       :index-of-parent-list="indexOfParentList">
        <div :key="widget.id" class="table-container"
           :class="[selected ? 'selected' : '', customClass]" @click.stop="selectWidget(widget)">
        <YxtTable
            :data="tableData"
            :table-columns="tableColumns"
            :show-action="widget.options.showAction"
            :rowbtns="columnBtns"
            :actions="handleTableActions"
            :zero-types="widget.options.zeroTypes"
            :is-loading="isLoading"
            >
            <template v-slot:isscope="scope">
           
            </template>
        </YxtTable>

        <!-- <yxt-pagination v-if="widget.options.showpage" :total="paginationConfig.total" :page.sync="paginationConfig.currentPage" :limit.sync="paginationConfig.currentSize" @pagination="handlePagination"/> -->
      </div>
  
    </container-wrapper>
  </template>
  
  <script>
    import ContainerWrapper from "@/components/form-designer/form-widget/container-widget/container-wrapper"
import containerMixin from "@/components/form-designer/form-widget/container-widget/containerMixin"
import TableCellWidget from "@/components/form-designer/form-widget/container-widget/table-cell-widget"
import refMixinDesign from "@/components/form-designer/refMixinDesign"
import i18n from "@/utils/i18n"
// import { BubbleTooltip } from "_quill@1.3.7@quill"
  
    export default {
      name: "yxt-table-widget",
      componentName: 'ContainerWidget',
      mixins: [i18n, containerMixin, refMixinDesign],
      inject: ['refList','globalData'],
      components: {
        ContainerWrapper,
        TableCellWidget,
      },
      props: {
        widget: Object,
        parentWidget: Object,
        parentList: Array,
        indexOfParentList: Number,
        designer: Object,
        isLoading:Boolean,
      },
      data() {
        return {
            tableData: [
            {id:1,name:'',old:'500'},
            ], //fieldchange
            tableColumns: [],
            zeroTypes:[],
            columnBtns: [],
        }
      },
      computed: {
        selected() {
          return this.widget.id === this.designer.selectedId
        },
  
        customClass() {
          return this.widget.options.customClass || ''
        },
  
      },
      watch: {
        //
      },
      created() {
        console.log("table ")
        this.initRefList()
        this.initTableOption()
      },
      mounted() {
        //
      },
      methods: {
        handleTableActions(query) {

        },
        initTableOption(){
          this.tableColumns = this.widget.options.tableColumns
        },
        updataColumns(){
          console.log('updata')
          this.tableColumns = []
          this.$nextTick(()=>{
            this.tableColumns = this.widget.options.tableColumns
          })
        },
        updataTable(type){
          switch (type) {
            case 'showIndex':
              this.compZeroType()
              break;

            case 'showCheckbox':
              this.compZeroType()
              break;

            case 'showpage':
              
              break;
            case 'showAction':
              
              break;
          }
        },
        compZeroType(){
            this.widget.options.zeroTypes=[]
            if(this.widget.options.showIndex){
            this.widget.options.zeroTypes.push({ label: '', type: 'index', width: '50', align: 'center', fixed: 'left' })
            }
            if(this.widget.options.showCheckbox){
            this.widget.options.zeroTypes.push({ label: '', type: 'selection', width: '50', align: 'center', fixed: 'left' })
            }
        }
      }
    }
  </script>
  
  <style lang="scss" scoped>
    div.table-container {
      padding: 5px;
      border: 1px dashed #336699;
      box-sizing: border-box;
  
      table.table-layout {
        width: 100%;
        text-align: center;
        //border: 1px solid #c8ebfb;
        border-collapse: collapse;
        table-layout: fixed;
  
        ::v-deep td {
          height: 48px;
          border: 1px dashed #336699;
          padding: 3px;
          display: table-cell;
        }
  
        .form-widget-list {
          border: 1px dashed #336699;
          min-height: 36px;
        }
      }
    }
  
    .table-container.selected {
      outline: 1px solid $--color-primary !important;
    }
  
  </style>
  