<template>
    <form-item-wrapper :designer="designer" :field="field" :rules="rules" :design-state="designState"
                       :parent-widget="parentWidget" :parent-list="parentList" :index-of-parent-list="indexOfParentList"
                       :sub-form-row-index="subFormRowIndex" :sub-form-col-index="subFormColIndex" :sub-form-row-id="subFormRowId">
                       <to-tag
                       v-for="(item,index) in field.options.optionItems" :key="item.value"
                        :closable="field.options.addAble"
                        @close="handleClose(index)"
                        >
                        {{item.label}}
                      </to-tag>
                      <div class="inline" v-if="field.options.addAble">
                        <el-input
                          class="input-new-tag"
                          v-if="inputVisible"
                          v-model="inputValue"
                          ref="saveTagInput"
                          size="small"
                          @keyup.enter.native="handleInputConfirm"
                          @blur="handleInputConfirm"
                        >
                        </el-input>
                        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
                      </div>
    </form-item-wrapper>
  </template>
  
  <script>
    import FormItemWrapper from './form-item-wrapper'
    import emitter from '@/utils/emitter'
    import i18n, {translate} from "@/utils/i18n";
    import fieldMixin from "@/components/form-designer/form-widget/field-widget/fieldMixin";
  
    export default {
      name: "tag-widget",
      componentName: 'FieldWidget',  //FieldWidgetbroadcast
      mixins: [emitter, fieldMixin, i18n],
      props: {
        field: Object,
        parentWidget: Object,
        parentList: Array,
        indexOfParentList: Number,
        designer: Object,
  
        designState: {
          type: Boolean,
          default: false
        },
  
        subFormRowIndex: { /* 0 */
          type: Number,
          default: -1
        },
        subFormColIndex: { /* 0 */
          type: Number,
          default: -1
        },
        subFormRowId: { /* Idid */
          type: String,
          default: ''
        },
  
      },

      components: {
        FormItemWrapper,
      },
      inject: ['refList', 'formConfig', 'globalOptionData', 'globalModel','apiRequets','globalData'],
      data() {
        return {
          oldFieldValue: null, //fieldchange
          fieldModel: null,
          rules: [],
          inputVisible:false,
          inputValue:''
        }
      },
      computed: {
        inputType() {
          if (this.field.options.type === 'number') {
            return 'text'  //inputtypenumberv-model
          }
  
          return this.field.options.type
        }
  
      },
      beforeCreate() {
        /*  */
      },
  
      created() {
        /* mountedcreatedmountedmountedprop
           created */
        this.initTagItems()
        this.initFieldModel()
        this.registerToRefList()
        this.initEventHandler()
        this.buildFieldRules()

        this.handleOnCreated()
        // 
        this.updataOptions()
      },
  
      mounted() {
        this.handleOnMounted()
      },
  
      beforeDestroy() {
        this.unregisterFromRefList()
      },
  
      methods: {
        handleClose(tag) {
          // onchange  newvalue,  oldvalue
          this.$emit('field-value-changed', [{index:tag,...this.field.options.optionItems[tag]},'delete'])

          //  fieldName, newValue, oldValue, subFormName, subFormRowIndex,signData
          this.dispatch('VFormRender', 'fieldChange', [this.field.options.name,'','','','',{
            sign:'tagClear',
            index:tag,
            row:this.field.options.optionItems[tag]
          }]);

          this.field.options.optionItems.splice(this.field.options.optionItems.indexOf(tag), 1);
        },

        showInput() {
          this.inputVisible = true;
          this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
          });
        },

        handleInputConfirm() {
          let inputValue = this.inputValue;
          if (inputValue) {
            this.field.options.optionItems.push(
              {label: inputValue, value: inputValue}
            );
            this.$emit('field-value-changed', [{label: inputValue, value: inputValue,index:this.field.options.optionItems.length},'add',])
            this.dispatch('VFormRender', 'fieldChange', [this.field.options.name,'','','','',{
            sign:'tagadd',
            index:this.field.options.optionItems.length,
            row:{label: inputValue, value: inputValue}
          }]);
          }
          this.inputVisible = false;
          this.inputValue = '';
        }
      }
    }
  </script>
  
  <style lang="scss" scoped>
    @import "../../../../styles/global.scss"; //* form-item-wrapper *//
  .to-tag{
    margin-right: 6px;
  }
  .inline{
    display: inline-block;
    margin-left: 8px;
  }
  </style>
  