<template>
    <static-content-wrapper :designer="designer" :field="field" :design-state="designState" :display-style="field.options.displayStyle"
                       :parent-widget="parentWidget" :parent-list="parentList" :index-of-parent-list="indexOfParentList"
                       :sub-form-row-index="subFormRowIndex" :sub-form-col-index="subFormColIndex" :sub-form-row-id="subFormRowId">
            <!-- isInline:true,
      showExpand:true,
      formBtnsFlex:'right',  // left right
      isAutoFix:true,
      isShowBtns:true, -->
           <YxtForm
                ref="yxtFormRef"
                :form-items="formItems"
                :formBtns = "formBtns"
                :form-key="formKey"
                :show-expand="field.options.showExpand"
                :is-auto-fix="false"
                :row-span="field.options.rowSpan"
                :label-width="field.options.labelWidth+'px'"
                :isFormDisabled="field.options.disabled"
                :labelPosition="field.options.labelPosition"
                :isInline='field.options.isInline'
                :showExpand="field.options.showExpand"
                :formBtnsFlex="field.options.formBtnsFlex"
                :isAutoFix = "field.options.isAutoFix"
                :isShowBtns = "field.options.isShowBtns"
                class="yxt-rule-form"
                @getFormValues="queryFormSubmit"
                @resetForm="resetForm"
            >
              <!-- <slot v-for="item in formItems" :name="item.id">yxtform</slot> -->

              <div v-for="item in formItems" :key="item.id" v-if="item.isscope" :slot="item.id" slot-scope="scope">
                <slot :name="'inner-'+item.id"  :scope="{field:field,item:item}"></slot>
              </div>

          </YxtForm>
    </static-content-wrapper>

</template>
  
  <script>

    import FormItemWrapper from '../form-item-wrapper'
    import StaticContentWrapper from '../static-content-wrapper'
    import ContainerWrapper from "@/components/form-designer/form-widget/container-widget/container-wrapper"
    import containerMixin from "@/components/form-designer/form-widget/container-widget/containerMixin"
    import TableCellWidget from "@/components/form-designer/form-widget/container-widget/table-cell-widget"
    import refMixinDesign from "@/components/form-designer/refMixinDesign"
    import i18n from "@/utils/i18n"
    import fieldMixin from "@/components/form-designer/form-widget/field-widget/fieldMixin";
    // import { BubbleTooltip } from "_quill@1.3.7@quill"
  
    export default {
      name: "yxt-form-widget",
      componentName: 'FieldWidget',
      mixins: [i18n, fieldMixin, i18n],
      inject: ['refList', 'formConfig', 'globalOptionData', 'globalModel','apiRequets','globalData'],
      components: {
        StaticContentWrapper
      },
      props: {
        field: Object,
        parentWidget: Object,
        parentList: Array,
        indexOfParentList: Number,
        designer: Object,
  
        designState: {
          type: Boolean,
          default: false
        },
  
        subFormRowIndex: { /* 0 */
          type: Number,
          default: -1
        },
        subFormColIndex: { /* 0 */
          type: Number,
          default: -1
        },
        subFormRowId: { /* Idid */
          type: String,
          default: ''
        },
  
      },
      data() {
        return {
          formItems:this.field.options.formItems,
          formBtns:this.field.options.formBtns,
          formKey:{}
        }
      },
      computed: {
        selected() {
          return this.widget.id === this.designer.selectedId
        },
  
        customClass() {
          return this.widget.options.customClass || ''
        },
  
      },
      watch: {
        //
      },
      created() {
      /* mountedcreatedmountedmountedprop
         created */
      this.registerToRefList()
      this.initEventHandler()

      this.handleOnCreated()
    },

    mounted() {
      this.handleOnMounted()
    },

    beforeDestroy() {
      this.unregisterFromRefList()
    },
      methods: {

        queryFormSubmit(){

        },
        resetForm(){
            
        }
      }
    }
  </script>
  
  <style lang="scss" scoped>
  @import "../../../../../styles/global.scss"; //* form-item-wrapper *//

</style>
  