<template>
  <form-item-wrapper :designer="designer" :field="field" :rules="rules" :design-state="designState"
                     :parent-widget="parentWidget" :parent-list="parentList" :index-of-parent-list="indexOfParentList"
                     :sub-form-row-index="subFormRowIndex" :sub-form-col-index="subFormColIndex" :sub-form-row-id="subFormRowId">
    <to-input-number ref="fieldEditor" v-model="fieldModel" class="full-width-input"
                     :disabled="field.options.disabled"
                     :size="field.options.size" :controls-position="field.options.controlsPosition"
                     :placeholder="field.options.placeholder"
                     :min="field.options.min" :max="field.options.max"
                     :precision="field.options.precision" :step="field.options.step"
                     @focus="handleFocusCustomEvent" @blur="handleBlurCustomEvent"
                     @change="handleChangeEvent">
    </to-input-number>
  </form-item-wrapper>
</template>

<script>
  import FormItemWrapper from './form-item-wrapper'
  import emitter from '@/utils/emitter'
  import i18n, {translate} from "@/utils/i18n";
  import fieldMixin from "@/components/form-designer/form-widget/field-widget/fieldMixin";

  export default {
    name: "number-widget",
    componentName: 'FieldWidget',  //FieldWidgetbroadcast
    mixins: [emitter, fieldMixin, i18n],
    props: {
      field: Object,
      parentWidget: Object,
      parentList: Array,
      indexOfParentList: Number,
      designer: Object,

      designState: {
        type: Boolean,
        default: false
      },

      subFormRowIndex: { /* 0 */
        type: Number,
        default: -1
      },
      subFormColIndex: { /* 0 */
        type: Number,
        default: -1
      },
      subFormRowId: { /* Idid */
        type: String,
        default: ''
      },

    },
    components: {
      FormItemWrapper,
    },
    inject: ['refList', 'formConfig', 'globalOptionData', 'globalModel','apiRequets','globalData'],
    data() {
      return {
        oldFieldValue: null, //fieldchange
        fieldModel: null,
        rules: [],
      }
    },
    computed: {

    },
    beforeCreate() {
      /*  */
    },

    created() {
      /* mountedcreatedmountedmountedprop
         created */

      this.initFieldModel()
      this.registerToRefList()
      this.initEventHandler()
      this.buildFieldRules()

      this.handleOnCreated()
      this.$nextTick(()=>{
        this.initDefaultVal()
      })
    },

    mounted() {
      this.handleOnMounted()
    },

    beforeDestroy() {
      this.unregisterFromRefList()
    },

    methods: {
      initDefaultVal(){
        if((this.globalModel.formModel&&this.globalModel.formModel[this.field.options.name])==''){
          this.globalModel.formModel[this.field.options.name]=0
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../../styles/global.scss"; //* form-item-wrapper *//

  ::v-deep .full-width-input {
    width: calc(100% - 20px) !important;
    .to-input{
      width: 100% !important;
    }
  }
