import {deepClone,formatValueByType} from "@/utils/util"
import FormValidators from '@/utils/validators'

export default {
  inject: ['refList', 'formConfig', 'globalOptionData', 'globalModel', 'getOptionData'],

  computed: {
    subFormName() {
      return this.parentWidget ? this.parentWidget.options.name : ''
    },

    subFormItemFlag() {
      return this.parentWidget ? this.parentWidget.type === 'sub-form' : false
    },

    formModel: {
      cache: false,
      get() {
        return this.globalModel.formModel
      }
    },

  },
  mounted() {
    this.initDataSource()
  },
  methods: {

    //---------------------  begin ------------------//
    getPropName() {
      if (this.subFormItemFlag && !this.designState) {
        return this.subFormName + "." + this.subFormRowIndex + "." + this.field.options.name + ""
      } else {
        return this.field.options.name
      }
    },

    initFieldModel() {
      if (!this.field.formItemFlag) {
        return
      }
      if (!!this.subFormItemFlag && !this.designState) {  //SubForm

        let subFormData = this.formModel[this.subFormName]
        if (((subFormData === undefined) || (subFormData[this.subFormRowIndex] === undefined) ||
            (subFormData[this.subFormRowIndex][this.field.options.name] === undefined)) &&
            (this.field.options.defaultValue !== undefined)) {
          this.fieldModel = this.field.options.defaultValue
          subFormData[this.subFormRowIndex][this.field.options.name] = this.field.options.defaultValue
        } else if (subFormData[this.subFormRowIndex][this.field.options.name] === undefined) {
          this.fieldModel = null
          subFormData[this.subFormRowIndex][this.field.options.name] = null
        } else {
          this.fieldModel = subFormData[this.subFormRowIndex][this.field.options.name]
        }

        /* field-widgetonChange */
        setTimeout(() => {  //onChange, 
          this.handleOnChangeForSubForm(this.fieldModel, this.oldFieldValue, subFormData, this.subFormRowId)
        }, 800)
        this.oldFieldValue = deepClone(this.fieldModel)

        this.initFileList()  //

        return
      }

      if ((this.formModel[this.field.options.name] === undefined) &&
          (this.field.options.defaultValue !== undefined)) {
        this.fieldModel = this.field.options.defaultValue
      } else if (this.formModel[this.field.options.name] === undefined) {  //formModelnull!!
        this.formModel[this.field.options.name] = null
      } else {
        this.fieldModel = this.formModel[this.field.options.name]
      }
      this.oldFieldValue = deepClone(this.fieldModel)
      this.initFileList()  //
    },

    initFileList() { //
      if ( ((this.field.type !== 'picture-upload') && (this.field.type !== 'file-upload')) || (this.designState === true) ) {
        return
      }

      if (this.fieldModel) {
        if (Array.isArray(this.fieldModel)) {
          this.fileList = deepClone(this.fieldModel)
        } else {
          this.fileList.splice(0, 0, deepClone(this.fieldModel))
        }
      }

      //  
    //   var str = "a${aa}bbb${ccc}dddeeefff";
    //   str = str.match(/(\$\{(.+?)\})/g);
    //   var obj = {
    //     name:'leaf',
    //     age:20
    // }
    // var str = "{{name}}name{{age}}";
    // function render(template, context) {
    //   return template.replace(/\{\{(.*?)\}\}/g, (match, key) => context[key.trim()]);
    // }
    // render(str,obj)
      let newUrl = this.field.options.uploadURL.replace(/\{\{(.*?)\}\}/g, (match, key) => this.globalData[key.trim()])
      if(newUrl.indexOf('undefined')>-1){
        this.$message.warning( `` )
      }
        this.field.options.uploadURL = newUrl
    },

    initEventHandler() {
      this.$on('setFormData', (newFormData) => {
        //console.log('formModel of globalModel----------', this.globalModel.formModel)
        if (!this.subFormItemFlag) {
          this.setValue(newFormData[this.field.options.name])
        }
      })

      this.$on('field-value-changed', (values) => {
        if (this.subFormItemFlag) {
          let subFormData = this.formModel[this.subFormName]
          this.handleOnChangeForSubForm(values[0], values[1], subFormData, this.subFormRowId)
        } else {
          this.handleOnChange(values[0], values[1])
        }
      })

      /*  */
      this.$on('reloadOptionItems', (widgetNames) => {
        if ((widgetNames.length === 0) || (widgetNames.indexOf(this.field.options.name) > -1)) {
          this.initOptionItems(true)
        }
      })
    },

    handleOnCreated() {
      if (this.field.options.onCreated) {
        let customFunc = new Function(this.field.options.onCreated)
        customFunc.call(this)
      }
    },

    handleOnMounted() {
      if (this.field.options.onMounted) {
        let mountFunc = new Function(this.field.options.onMounted)
        mountFunc.call(this)
      }
    },

    registerToRefList(oldRefName) {
      if ((this.refList !== null) && !!this.field.options.name) {
        if (this.subFormItemFlag && !this.designState) { //
          if (oldRefName) {
            delete this.refList[oldRefName + '@row' + this.subFormRowId]
          }
          this.refList[this.field.options.name + '@row' + this.subFormRowId] = this
        } else {
          if (oldRefName) {
            delete this.refList[oldRefName]
          }
          this.refList[this.field.options.name] = this
        }
      }
    },

    unregisterFromRefList() {  //ref
      if ((this.refList !== null) && !!this.field.options.name) {
        let oldRefName = this.field.options.name
        if (this.subFormItemFlag && !this.designState) { //
          delete this.refList[oldRefName + '@row' + this.subFormRowId]
        } else {
          delete this.refList[oldRefName]
        }
      }
    },

    initOptionItems(keepSelected) {
      console.log('initOptionItems')
      if (this.designState) {
        return
      }

      if ((this.field.type === 'radio') || (this.field.type === 'checkbox')
          || (this.field.type === 'select') || (this.field.type === 'cascader')) {
        /* option-dataglobalOptionDataprovidegetOptionData() */
        const newOptionItems = this.getOptionData()
        if (!!newOptionItems && newOptionItems.hasOwnProperty(this.field.options.name)) {
          if (keepSelected) {
            this.reloadOptions(newOptionItems[this.field.options.name])
          } else {
            this.loadOptions(newOptionItems[this.field.options.name])
          }
        }
      }
    },

    // tag
    initTagItems(keepSelected) {
      // if (this.designState) {
      //   return
      // }
      if ((this.field.type === 'tag') ) {
        /* option-dataglobalOptionDataprovidegetOptionData() */
        const newOptionItems = this.getOptionData()
        if (!!newOptionItems && newOptionItems.hasOwnProperty(this.field.options.name)) {
          if (keepSelected) {
            this.reloadOptions(newOptionItems[this.field.options.name])
          } else {
            this.loadOptions(newOptionItems[this.field.options.name])
          }
        }
      }
    },

    refreshDefaultValue() {
      if ((this.designState === true) && (this.field.options.defaultValue !== undefined)) {
        this.fieldModel = this.field.options.defaultValue
      }
    },

    clearFieldRules() {
      if (!this.field.formItemFlag) {
        return
      }

      this.rules.splice(0, this.rules.length)  //
    },

    buildFieldRules() {
      if (!this.field.formItemFlag && this.field.options.hidden) {
        return
      }

      this.rules.splice(0, this.rules.length)  //
      
      //  
      if (this.field.options.minLength) {
        this.rules.push({
          min:this.field.options.minLength,
          trigger: 'blur', 
          message: ''+this.field.options.minLength+'',
        })
      }

      if (this.field.options.required) {
        this.rules.push({
          required: true,
          //trigger: ['blur', 'change'],
          trigger: ['blur'],  /* changechangeformModelradio/checkbox */
          message: this.field.options.requiredHint || this.i18nt('render.hint.fieldRequired'),
        })
      }

      if (this.field.options.validation) {
        let vldName = this.field.options.validation
        if (FormValidators[vldName]) {
          this.rules.push({
            validator: FormValidators[vldName],
            trigger: ['blur', 'change'],
            label: this.field.options.label,
            errorMsg: this.field.options.validationHint
          })
        } else {
          this.rules.push({
            validator: FormValidators['regExp'],
            trigger: ['blur', 'change'],
            regExp: vldName,
            label: this.field.options.label,
            errorMsg: this.field.options.validationHint
          })
        }
      }

      if (this.field.options.onValidate) {
        let customFn = (rule, value, callback) => {
          let tmpFunc =  new Function('rule', 'value', 'callback', this.field.options.onValidate)
          return tmpFunc.call(this, rule, value, callback)
        }
        this.rules.push({
          validator: customFn,
          trigger: ['blur', 'change'],
          label: this.field.options.label
        })
      }
    },

    /**
     * 
     */
    disableChangeValidate() {
      if (!this.rules) {
        return
      }

      this.rules.forEach(rule => {
        if (rule.trigger) {
          rule.trigger.splice(0, rule.trigger.length)
        }
      })
    },

    /**
     * 
     */
    enableChangeValidate() {
      if (!this.rules) {
        return
      }

      this.rules.forEach(rule => {
        if (rule.trigger) {
          rule.trigger.push('blur')
          rule.trigger.push('change')
        }
      })
    },

    disableOptionOfList(optionList, optionValue) {
      if (!!optionList && (optionList.length > 0)) {
        optionList.forEach(opt => {
          if (opt.value === optionValue) {
            opt.disabled = true
          }
        })
      }
    },

    enableOptionOfList(optionList, optionValue) {
      if (!!optionList && (optionList.length > 0)) {
        optionList.forEach(opt => {
          if (opt.value === optionValue) {
            opt.disabled = false
          }
        })
      }
    },

    //---------------------  end ------------------//

    //---------------------  begin ------------------//

    emitFieldDataChange(newValue, oldValue) {
      this.$emit('field-value-changed', [newValue, oldValue])

      /* dispatch */
      this.dispatch('VFormRender', 'fieldChange',
          [this.field.options.name, newValue, oldValue, this.subFormName, this.subFormRowIndex])
    },

    syncUpdateFormModel(value) {
      if (this.designState) {
        return
      }

      if (this.subFormItemFlag) {
        let subFormData = this.formModel[this.subFormName] || [{}]
        let subFormDataRow = subFormData[this.subFormRowIndex]
        subFormDataRow[this.field.options.name] = value
      } else {
        this.formModel[this.field.options.name] = value
      }
    },

    handleChangeEvent(value) {  /* inputhandleChangeEventhandleInputCustomEvent */
      this.syncUpdateFormModel(value)
      this.emitFieldDataChange(value, this.oldFieldValue)

      //numberfocusoldFieldValue
      this.oldFieldValue = deepClone(value)  /* oldFieldValueinitFieldModel()!! */

      /*  */
      this.dispatch('VFormRender', 'fieldValidation', [this.getPropName()])
    },

    handleFocusCustomEvent(event) {
      this.oldFieldValue = deepClone(this.fieldModel)  //change

      if (this.field.options.onFocus) {
        let customFn = new Function('event', this.field.options.onFocus)
        customFn.call(this, event)
      }
    },

    handleBlurCustomEvent(event) {
      if (this.field.options.onBlur) {
        let customFn = new Function('event', this.field.options.onBlur)
        customFn.call(this, event)
      }
    },

    handleInputCustomEvent(value) {
      this.syncUpdateFormModel(value)

      /*  */
      this.dispatch('VFormRender', 'fieldValidation', [this.getPropName()])

      if (this.field.options.onInput) {
        let customFn = new Function('value', this.field.options.onInput)
        customFn.call(this, value)
      }
    },

    emitAppendButtonClick() {
      if (this.designState) { //
        return
      }

      if (this.field.options.onAppendButtonClick) {
        let customFn = new Function(this.field.options.onAppendButtonClick)
        customFn.call(this)
      } else {
        /* mixinsdispatch */
        this.dispatch('VFormRender', 'appendButtonClick', [this])
      }
    },

    emitCustomMsg(componentName,action,data){
      if (this.designState) { //
        return
      }

      if (this.field.options.onAppendButtonClick) {
        let customFn = new Function(this.field.options.onAppendButtonClick)
        customFn.call(this)
      } else {
        console.log('emitCustomMsg')
        /* mixinsdispatch */
        this.dispatch(componentName, action, [data])
      }
    },

    handleOnChange(val, oldVal) {  //onChange
      if (this.field.options.onChange) {
        let changeFn = new Function('value', 'oldValue', this.field.options.onChange)
        changeFn.call(this, val, oldVal)
      }
    },

    handleOnChangeForSubForm(val, oldVal, subFormData, rowId) {  //onChange
      if (this.field.options.onChange) {
        let changeFn = new Function('value', 'oldValue', 'subFormData', 'rowId', this.field.options.onChange)
        changeFn.call(this, val, oldVal, subFormData, rowId)
      }
    },

    handleButtonWidgetClick() {
      if (this.designState) { //
        return
      }

      if (this.field.options.onClick) {
        let changeFn = new Function(this.field.options.onClick)
        changeFn.call(this)
      } else {
        this.dispatch('VFormRender', 'buttonClick', [this]);
      }
    },

    remoteQuery(keyword) {
      if (this.field.options.onRemoteQuery) {
        let remoteFn = new Function('keyword', this.field.options.onRemoteQuery)
        remoteFn.call(this, keyword)
      }
    },

    //---------------------  end ------------------//

    //--------------------- API begin ------------------//
    /*  */

    getFormRef() { /* VFromVFormcreated */
      return this.refList['v_form_ref']
    },

    getWidgetRef(widgetName, showError) {
      let foundRef = this.refList[widgetName]
      if (!foundRef && !!showError) {
        this.$message.error(this.i18nt('render.hint.refNotFound') + widgetName)
      }
      return foundRef
    },

    getFieldEditor() { //el
      return this.$refs['fieldEditor']
    },

    /*
      VFormRendersetFormDatafield-widgetsetValue
      setFormDatafield-widgetsetFormData
    * */
    setValue(newValue) {
      /* if ((this.field.type === 'picture-upload') || (this.field.type === 'file-upload')) {
        this.fileList = newValue
      } else */ if (this.field.formItemFlag) {
        let oldValue = deepClone(this.fieldModel)
        this.fieldModel = newValue
        this.initFileList()

        this.syncUpdateFormModel(newValue)
        this.emitFieldDataChange(newValue, oldValue)
      }
    },

    getValue() {
      /* if ((this.field.type === 'picture-upload') || (this.field.type === 'file-upload')) {
        return this.fileList
      } else */ {
        return this.fieldModel
      }
    },

    resetField() {
      if (this.subFormItemFlag) { //
        return
      }

      let defaultValue = this.field.options.defaultValue
      this.setValue(defaultValue)

      //
      if ((this.field.type === 'picture-upload') || (this.field.type === 'file-upload')) {
        this.$refs['fieldEditor'].clearFiles()
        this.fileList.splice(0, this.fileList.length)
      }
    },

    setWidgetOption(optionName, optionValue) { //API
      if (this.field.options.hasOwnProperty(optionName)) {
        this.field.options[optionName] = optionValue
        //TODO: 
      }
    },

    setReadonly(flag) {
      this.field.options.readonly = flag
    },

    setDisabled(flag) {
      this.field.options.disabled = flag
    },

    setAppendButtonVisible(flag) {
      this.field.options.appendButton = flag
    },

    setAppendButtonDisabled(flag) {
      this.field.options.appendButtonDisabled = flag
    },

    setHidden(flag) {
      this.field.options.hidden = flag

      if (flag) {  //
        this.clearFieldRules()
      } else {  //
        this.buildFieldRules()
      }
    },

    setRequired(flag) {
      this.field.options.required = flag
      this.buildFieldRules()
    },

    setLabel(newLabel) {
      this.field.options.label = newLabel
    },

    focus() {
      if (!!this.getFieldEditor() && !!this.getFieldEditor().focus) {
        this.getFieldEditor().focus()
      }
    },

    clearSelectedOptions() {  //
      if ((this.field.type !== 'checkbox') && (this.field.type !== 'radio') && (this.field.type !== 'select')) {
        return
      }

      if ((this.field.type === 'checkbox') ||
          ((this.field.type === 'select') && this.field.options.multiple)) {
        this.fieldModel = []
      } else {
        this.fieldModel = ''
      }
    },

    /**
     * 
     * @param options
     */
    loadOptions(options) {
      this.field.options.optionItems = deepClone(options)
      //this.clearSelectedOptions()  //
    },

    /**
     * 
     * @param options
     */
    reloadOptions(options) {
      this.field.options.optionItems = deepClone(options)
    },

    /**
     * radio/checkbox/select/cascader
     * @returns 
     */
    getOptions() {
      return this.field.options.optionItems
    },

    disableOption(optionValue) {
      this.disableOptionOfList(this.field.options.optionItems, optionValue)
    },

    enableOption(optionValue) {
      this.enableOptionOfList(this.field.options.optionItems, optionValue)
    },

    setUploadHeader(name, value) {
      this.$set(this.uploadHeaders, name, value)
    },

    deleteUploadHeader(key){
      this.$delete(this.uploadHeaders, key);
    },

    setUploadData(name, value) {
      this.$set(this.uploadData, name, value)
    },

    deleteUploadData(key){
      this.$delete(this.uploadData, key);
    },

    setUploadUrl(value){
      if (this.designState) {
        return
      }

      if ((this.field.type === 'picture-upload') || (this.field.type === 'file-upload')){
        this.$set(this.uploadData, value)
      }
    },

    setToolbar(customToolbar) {
      this.customToolbar = customToolbar
    },

    /**
     * 
     * @returns {boolean}
     */
    isSubFormItem() {
      return this.parentWidget ? this.parentWidget.type === 'sub-form' : false
    },

    /**
     * css
     * @param className
     */
    addCssClass(className) {
      if (!this.field.options.customClass) {
        this.field.options.customClass = [className]
      } else {
        this.field.options.customClass.push(className)
      }
    },

    /**
     * css
     * @param className
     */
    removeCssClass(className) {
      if (!this.field.options.customClass) {
        return
      }

      let foundIdx = -1
      this.field.options.customClass.map((cc, idx) => {
        if (cc === className) {
          foundIdx = idx
        }
      })
      if (foundIdx > -1) {
        this.field.options.customClass.splice(foundIdx, 1)
      }
    },

     /**
   * 
   * request
   */
  updataOptions(){
    
    if (this.designState) {
      return
    }

    //  
    if(this.field.options.optionType==='dynamic'){
      this.getDictValue()
    }

    // API
    if(this.field.options.optionType==='api'){
      this.getApiMsg()
    }
  },

  //  
  getDictValue(){
    if(this.globalData.token&&this.apiRequets.getDictValue){
      
      let dictConfig = {
        params:{
          // keyCode:this.selectDict.keyCode
          keyCode:this.field.options.keyCode  
        },
        headers:{
          accessToken:this.globalData.token,
          ...this.apiRequets.getDictValue.dictHeader,
        }
      }
      let dictData = {
        keyCode:this.field.options.keyCode  
      }
      window.axios[this.apiRequets.getDictValue.methods](this.apiRequets.getDictValue.dictUrl,dictConfig).then(res => {
        if(res&&res.data&&res.data.code==0){
          //     this.field.options.optionItems = deepClone(options)
          let requestRes=res.data.data
          if(this.field.options.optionItems&&this.field.options.optionItems.length){
            this.field.options.optionItems.forEach(item=>{
              let getfilterObj = requestRes.filter(filterItem=>item.value===filterItem[this.field.options.dictKey])
               
               item.label =item.label && (getfilterObj&&getfilterObj[0]&&getfilterObj[0].labelZhCh)
            })
          }
          this.reloadOptions(this.field.options.optionItems)

        }else{
          this.$message.error(res.data.msg)
        }
      }).catch(error => {
        this.$message.error('')
      })
    }
  },

  //  API
  getApiMsg(tip){
    //    dictData    GW_202211151611504457
    
    let DataTrs = {},postmsg='';
    this.field.options.dictApi.dictDataMap.forEach(item=>{
      if(item.type==='widget'){
        if(item.value===''){
          postmsg = ''
          return
        }
        
        DataTrs[item.label] = this.getWidgetRef(item.value).getValue()
      }
      if(item.type==='global'){
        DataTrs[item.label] = item.value.toString().replace(/\{\{(.*?)\}\}/g, (match, key) => this.globalData[key.trim()])
        if(DataTrs[item.label]=='undefined'){
          postmsg = ''
          return
        }
      }
      DataTrs[item.label] = formatValueByType(DataTrs[item.label],item.format)
    })
    if(postmsg!==''){
      // this.$message({
      //   message: 'API[-]',
      //   type: 'warning'
      // });
      console.log('API[-]'+this.field.options.name)
      return
    }
    let dictUrl= this.field.options.dictApi.dictUrl.toString().replace(/\$\{\{(.*?)\}\}/g, (match, key) => this.getWidgetRef(key).getValue()) 
    if(/\$\{\{(.*?)\}\}/g.test(dictUrl)) {
      //  this.$message({
      //           message: 'API',
      //           type: 'warning'
      //         });
      console.log('API'+this.field.options.name)
      return
    }
        
        let dictConfig = {
          params:{
              ...DataTrs
            },
            headers:{
              accessToken:this.globalData.token,
              ...this.field.options.dictApi.dictHeader
            }
        }
        let dictData = {
          ...DataTrs
        }

      window.axios[this.field.options.dictApi.methods](dictUrl,dictConfig).then(res => {
          if(res&&res.data&&res.data.code==0){
            
            this.field.options.optionItems = []
            let resData = (this.field.options.dictApi.hierarchy&&this.field.options.dictApi.hierarchy!=='')?res.data.data[this.field.options.dictApi.hierarchy]:res.data.data
            // if(tip&&resData&&resData.length==0){
            //   this.$message({
            //     message: '',
            //     type: 'warning'
            //   });
            //   return
            // }
            resData&&resData.forEach(item=>{
              if(item.children){
                this.computChild(item.children)
              }
              if(item.children&&item.children.length==0){
                delete item.children
              }
              // return {
                // ...item,
                item.label=item[this.field.options.dictApi.prop.label]
                item.value=item[this.field.options.dictApi.prop.value]
              // }
            })

            this.reloadOptions(resData)
          }else{
            this.$message.error(res.data.msg)
          }
        }).catch(error => {
            this.$message.error('')
        })

    },
    computChild(data){
      
      data.forEach(item=>{
        if(item.children){
          this.computChild(item.children)
        }
        if(item.children&&item.children.length==0){
              delete item.children
            }
        item.label = item[this.field.options.dictApi.prop.label],
        item.value = item[this.field.options.dictApi.prop.value]
      })

    },

    initDataSource(){
      if (this.designState) {
        return
      }
      if((this.field.options.onDataSource===undefined)||(this.field.options.onDataSource &&this.field.options.onDataSource.length==0)){
        return
      }
      this.field.options.onDataSource&&this.field.options.onDataSource.forEach(item=>{
        if(item.function){
          let parameter = this.formatMsg(item)
          if(item.type==='api'){
            this.apiRequets(parameter)
          }else{
            switch(item.method){
              case 'get':
                  this.getRequest(parameter)
                  break;
              case 'post':
                  this.postRequest(parameter)
                  break;
              case 'put':
                  this.putRequest(parameter)
                  break;
              case 'delete':
                  this.deleteRequest(parameter)
                  break;
            }
          }
        }
        let customFunc = new Function(item.function)
        customFunc.call(this)
      })
    },
    traString(str){
      let newStr = str.toString().replace(/\{\{(.*?)\}\}/g, (match, key) => this.globalData[key.trim()])
      newStr = str.toString().replace(/\$\{\{(.*?)\}\}/g, (match, key) => this.$refs.formRef.getWidgetRef(key).getValue()) 
      return newStr
    },
    formatMsg(msg){
      let url = traString(msg.url)
      let headerOpt = {}
      let paramsOpt = {}
      let dataOpt = {}
  
      msg.headers.forEach(item=>{
        let key = item.find(list=>list.id=='name')[0].value
        let format = item.find(list=>list.id=='type')[0].value
        let value = item.find(list=>list.id=='value')[0].value
        value = this.traString(value)
        headerOpt[key] = formatValueByType(value,format)
      })
      msg.params.forEach(item=>{
        let key = item.find(list=>list.id=='name')[0].value
        let format = item.find(list=>list.id=='type')[0].value
        let value = item.find(list=>list.id=='value')[0].value
        value = this.traString(value)
        paramsOpt[key] = formatValueByType(value,format)
      })
      msg.data.forEach(item=>{
        let key = item.find(list=>list.id=='name')[0].value
        let format = item.find(list=>list.id=='type')[0].value
        let value = item.find(list=>list.id=='value')[0].value
        value = this.traString(value)
        dataOpt[key] = formatValueByType(value,format)
      })
      let parameter = {
        url:url,
        headers:{
          accessToken:this.globalData.token,
          headerOpt,
        },
        params:paramsOpt,
        data:dataOpt,
        function:msg.function(),
      }

      return parameter
    },
    getRequest(parameter){
      window.axios.get(parameter.url,{params:parameter.params,headers:parameter.headers}).then(res => {
        console.log(res)
        this.parameter.function(res)
      }).catch(error => {
        this.$message.error('')
      })
    },
    postRequest(parameter){
      window.axios.post(parameter.url,parameter.data,{ headers: parameter.headers}).then((res) => { 
        console.log(res)
        this.parameter.function(res)
      }).catch(error => {
        this.$message.error('')
      });

    },
    putRequest(parameter){
      window.axios.put(parameter.url,{params:parameter.params},{headers: parameter.headers}).then((res) => { 
        console.log(res)
        this.parameter.function(res)
      }).catch(error => {
        this.$message.error('')
      });
    },
    deleteRequest(parameter){
      window.axios.delete(parameter.url,{params:parameter.params,headers:parameter.headers}).then((res) => { 
        console.log(res)
        this.parameter.function(res)
      }).catch(error => {
        this.$message.error('')
      });
    },
    apiRequet(parameter){
      //   
      this.apiRequets(parameter).then((res) => { 
        this.parameter.function(res)
      }).catch(error => {
        this.$message.error('')
      });
    }
    //--------------------- API end ------------------//
  },
}
